	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-05-13-blackmarket-bread/index.md absPath of file >>> MarkdownRemark","html":"<p>Listening to the news in the car this morning there are protests in Egypt because the high price of fuel has driven up the cost of grain which has driven up the cost of government subsidized bread.\nThe news story was the first time I had heard the term \"Black Market Bread.\" As I drove down the freeway amongst thousands of other cars I realized that the high price of fuel is an inconvenience\nfor many of us here in the United States but it can mean starvation for those who already have limited access to resources.</p>","frontmatter":{"title":"Blackmarket Bread","date":"May 13, 2008","imageUrl":null,"image":null}}},"pathContext":{"path":"/blackmarket-bread"}}

